












#fullbody{
   
  position: relative;
  top:0;
  left:8%;
  margin: 0;
  padding: 5%;
  padding-left: 0;
  width: 80%;
  box-shadow: 5px 0 20px 1px #000000;

}
#instructions p{
  font-family: 'Signika', sans-serif;
  font-size:30px;
  text-align: center;
}


#instruction_lines{
  position:relative;
  top:0; 
  left:30%;
  width:60%;
  font-size:100%;
  text-align: justify;
  font-family: 'Signika', sans-serif;
}
#finishing_inst_line{
  position:relative;
  height: 10%;
  font-family: 'Signika', sans-serif;
  margin:5%;
  font-size:150%;
  text-align: center;
}

.area{
  display: -webkit-flex;
  display: flex;
  flex-direction: row; 
  
}
.ques{
  width:40%;
  height:100%;
  font-size: 100%;
  border-style: solid;
  
  margin:5%;
  box-shadow: 1px 10px 10px black;
  border-radius: 5%;
  transition: transform .3s ease-in-out;
}
.ques:hover{
  transform: scaleX(1.1) scaleY(1.1) scaleZ(1.1);
}
.qbox{
  position: relative;
  top:0;
  left:2%;
  width:100%;
  height:34%;
}
.qbox p{
  text-align: center;
}
.options{
  position: relative;
  top:0;
  left:2%;
  width: 100%;
  height:66%;
  display: -webkit-flex;
  display: flex;
  flex-direction: column; 

}
._op_div{
  position:relative;
  top:0;
  left:0;
  width:100%;
  height:50%;
  display: -webkit-flex;
  display: flex;
  flex-direction: row; 
}
.option{
  pointer-events: auto;
  position: relative;
  top:0;
  left:0;
  width:50%;
  height:100%;
  margin:10%;
  padding-left: 10%;
  border-radius: 6%;
  box-shadow: 1px 5px 15px black;
  transition: transform .3s ease-in-out;
}
.option_click{
  pointer-events: auto;
  position: relative;
  top:0;
  left:0;
  width:50%;
  height:100%;
  margin:10%;
  padding-left: 10%;
  border-radius: 6%;
  box-shadow: 1px 5px 15px black;
  transition: transform .3s ease-in-out;
  background-color: chartreuse;
  border-color: coral;
  border-width: 10px;
}
.selected_correct{
  pointer-events: auto;
  position: relative;
  top:0;
  left:0;
  width:50%;
  height:100%;
  margin:10%;
  padding-left: 10%;
  border-radius: 6%;
  box-shadow: 1px 5px 15px black;
  transition: transform .3s ease-in-out;
  background-color: purple;
  border-color: coral;
  border-width: 10px;
}
.selected_incorrect{
  pointer-events: auto;
  position: relative;
  top:0;
  left:0;
  width:50%;
  height:100%;
  margin:10%;
  padding-left: 10%;
  border-radius: 6%;
  box-shadow: 1px 5px 15px black;
  transition: transform .3s ease-in-out;
  background-color: red;
  border-color: coral;
  border-width: 10px;
}
.notselected_correct{
  pointer-events: auto;
  position: relative;
  top:0;
  left:0;
  width:50%;
  height:100%;
  margin:10%;
  padding-left: 10%;
  border-radius: 6%;
  box-shadow: 1px 5px 15px black;
  transition: transform .3s ease-in-out;
  background-color: chartreuse;
  border-color: coral;
  border-width: 10px;
}
.option:hover{
  transform: scaleX(1.1) scaleY(1.1) scaleZ(1.1);
}

#area_2{
  display: -webkit-flex;
  display: flex;
  flex-direction: row; 

  
}
#answer{
  position: relative;
  top:5%;
  left:42%;
  padding:1.25%;
  padding-left: 5%;
  padding-right: 5%;

}
p {
 
  pointer-events: none;
}
#but{
  position: relative;
  
}
@media only screen and (max-width: 880px){
  .area{
      display: -webkit-flex;
      display: flex;
      flex-direction: column;
  }
  .ques{
      width:90%;
      margin-bottom: 10%;
      font-size: 80%;
  }
  #instructions p{
    font-family: 'Teko', sans-serif;
    font-size:20px;
    text-align: center;
  }
  #instruction_lines{
    position:relative;
    top:0; 
    left:20%;
    font-size:80%;    
  }
  #finishing_inst_line{
    font-size: 80%;
  }
  
}
